@import url(https://fonts.googleapis.com/css2?family=Libre+Barcode+128+Text&family=Karla&family=Roboto&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}


nav {
    background-image: url(/static/media/nav_background.f57c5a8b.jpg);
    background-position: center;
    background-size: cover;
    height: 50%;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 1;
}

.nav-container {
    background-image: linear-gradient(to right, white, 90%, transparent);
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    padding: 0 7%;
    width: 40%;
}

.nav-list {
    font-size: 1.5rem;
    font-weight: bold;
    line-height: 1.4;
}

.nav-list a {
    color: rgb(0, 0, 0);
}

.socials-list {
    /* border: 1px solid black; */
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding-top: 10px;
    width: 75px;
}

.socials-list li {
    margin-right: 5px;
    margin-top: 5px;
}
.nav-button {
    cursor: pointer;
    height: 25px;
    position: fixed;
    top: 20px;
    right: 20px;
    width: 30px;
    z-index: 1;
}

.nav-button span {
    border: 1px solid rgb(0, 0, 0);
    border-radius: 3px;
    background: rgb(255, 255, 255);
    height: 5px;
    position: absolute;
    width: 100%;
}

.top {
    top: 0;
    transform-origin: 0 0;
}

.middle {
    top: 10px;
}

.bottom {
    top: 20px;
    transform-origin: 0 0;
}

