.cover {
    text-align: center;
    position: relative;
    bottom: 8%;
    width: 100%;
}

h1 {
    color: rgb(238, 238, 238);
    font-size: 1.5rem;
    margin-bottom: 10px;
}

p {
    color: rgb(255, 249, 222);
}

.listen {
    border-bottom: 1px solid white;
    margin: 5px auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.listen p {
    color: white;
    font-size: .8rem;
    font-style: normal;
}

.album-cover {
    border: 1px solid rgb(255, 255, 255);
    box-shadow: 3px 3px 5px rgba(69, 68, 68, 0.8);
    margin: 0 auto;
    margin-top: 5px;
    width: 75%;
}

.buy {
    cursor: pointer;
    position: relative;
    bottom: 14%;
    margin: 0 auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.barcode {
    box-shadow: 1px 1px 3px rgb(67, 67, 67);
    box-sizing: content-box;
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
    font-family: 'Libre Barcode 128 Text', cursive;
    font-size: 2rem;
    font-style: normal;
    padding: 1px 12px;
}

.patreon {
    border-bottom: 1px solid rgb(255, 255, 255);
    border-top: 1px solid rgb(255, 255, 255);
    font-size: 1.1rem;
    margin: 0 auto;
    padding: 3px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.patreon p {
    background-color: rgba(255, 249, 222, .6);
    color: rgb(46, 12, 70);
    font-size: 1rem;
    font-style: normal;
    font-weight: 600;
    padding: 2px;
}

/* #patreon {
    text-shadow: 2px 2px 3px white;
} */

/* .wink {
    font-family: sans-serif;
    font-style: normal;
} */

@media screen and (min-width: 500px) {
    .barcode {
        padding: 2px 20px;
    }
}
@media screen and (min-width: 700px) {
    .barcode {
        font-size: 2.5rem;
        padding: 3px 30px;
    }
}


.merch-wrapper {
    position: relative;
}
.merch-container {
    border: 2px solid rgba(196, 247, 164, .8);
    display: flex;
    /* flex: none;
    flex-flow: row nowrap; */
    margin: 30px auto;
    overflow: scroll;
    scroll-behavior: smooth;
    -webkit-scroll-snap-type: x mandatory;
        -ms-scroll-snap-type: x mandatory;
            scroll-snap-type: x mandatory;
    width: 90%;
}

.img-container {
    background-color: white;
    flex: none;
    padding: 10px 10px 5px 10px;
    scroll-snap-align: center;
    width: 100%;
}

.img-container img {
    width: 100%;
}

.arrow {
    background-color:  rgba(196, 247, 164, 0.6);
    /* border: 1px solid springgreen; */
    box-sizing: content-box;
    color: rgba(46, 12, 70, .7);
    cursor: pointer;
    font-size: 2.2rem;
    position: absolute;
    bottom: 45%;
    -webkit-text-fill-color: white;
    -webkit-text-stroke: 1px rgb(46, 12, 70);
    width: 10%;
}

.scrollLeft {
    left: 0;
    padding: 6px 4px 6px 0;
}

.scrollRight {
    right: 0;
    padding: 6px 0 6px 4px;
}

.go-store {
    border-bottom: 1px solid rgb(255, 255, 255);
    border-top: 1px solid rgb(255, 255, 255);
    font-size: 1.1rem;
    margin: 0 auto;
    margin-bottom: 10px;
    padding: 3px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.go-store p {
    background-color: rgba(196, 247, 164, 0.6);
    color: rgb(46, 12, 70);
    font-size: .8rem;
    font-style: normal;
    font-weight: 600;
    padding: 2px;
}

.treat {
    color: rgb(132, 28, 206)
}
.no-events {
    /* border-bottom: 1px solid rgb(255, 255, 255);
    border-top: 1px solid rgb(255, 255, 255); */
    padding: 5px 0;
    position: relative;
    margin: 30px auto;
    height: 100px;
    width: 100%;
    z-index: 2;
}

.note {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
}

.no-events p {
    color: rgb(255, 255, 255);
    line-height: 1.6;
}

.no-events a {
    color: rgb(255, 255, 255);
    text-decoration: underline;
}

.no-events a:visited {
    color: rgb(255, 255, 255);
    text-decoration: underline;
}
.subscribe-container {
    /* background-color: rgb(238, 197, 183); */
    color: rgb(46, 12, 70);
    font-size: .9rem;
    padding: 40px;
    text-align: center;
}

.subscribe {
    /* background-color: rgba(238, 197, 183, .8); */
    border-bottom: 1px solid rgb(255, 255, 255);
    border-top: 1px solid white;
    margin: 0 auto;
    padding: 10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.subscribe form {
    margin: 5px 0;
}

input {
    border: 1px solid white;
    padding: 2px 10px;
    margin: 10px 3px;
}

.subscribe-btn {
    background-color: rgba(196, 247, 164, 0.6);
}
.contact {
    margin: 10px 0;
}

.contact a {
    color: white;
}

.title {
    color: white;
}
a .ViewProfileButton {
    background-color: aquamarine;
}
