}

#merch h2 {
    color: rgb(46, 12, 70);
    margin-bottom: 30px;
    /* text-shadow: 1px 1px 2px whitesmoke; */
}

#shows {
    background-image: url(/static/media/shows.0d029fd9.png);
    background-position: bottom;
    background-size: cover;
    height: 500px;
    padding: 50px 0;
    text-align: center;
}

#shows h2 {
    color: rgb(255, 255, 255);
    font-size: 1.4rem;
    /* text-shadow: 1px 1px 2px rgb(147, 0, 147); */
}

#fan-art {
    text-align: center;
}

